import { Component, ElementRef, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { MenuController, Platform } from '@ionic/angular';
import { Subscription, interval } from 'rxjs';
import { CredenciaisDTO } from '../models/credenciais.dto';
import { AuthService } from '../services/auth.service';
import { FundoService } from '../services/utils/fundo.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss'],
})
export class LoginPage implements OnInit {

  img = "";
  currentPlataform = '';

  listener: Subscription;

  creds: CredenciaisDTO = {
    email: "",
    senha: ""
  };
  res;
  constructor(
    private route: ActivatedRoute, private router: Router,
    private menu: MenuController, public auth: AuthService,
    private elementRef: ElementRef, private plat: Platform,
    private fundoService: FundoService) { }

  async ngOnInit() {
    await this.plat.ready()
      .then(() => {
        if (this.plat.width() <= 700)
          this.fundoService.setCurrentPlataform('mobile');
        else
          this.fundoService.setCurrentPlataform('pc');

        this.img = this.fundoService.getFirtsImg();
      })

    const source = interval(8000);
    this.listener = source.subscribe(val => {
      this.img = this.fundoService.changeImg()
    });
  }

  ionViewWillEnter() {
    this.menu.enable(false)
  }

  ionViewWillLeave() {
    this.menu.enable(true)
  }

  ionViewDidEnter() {
    this.auth.refreshToken()
      .subscribe(res => {
        this.auth.loginSucesso(res.headers.get('Authorization'));
        this.router.navigateByUrl('home');
      }, error => { });
  }

  login() {
    this.auth.authenticate(this.creds)
      .subscribe(res => {
        this.auth.loginSucesso(res.headers.get('Authorization'));
        this.router.navigateByUrl('home');
      }, error => { });
  }

  registrar() {
    this.router.navigateByUrl('registrar');
  }

  recovery() {
    this.router.navigateByUrl('recovery');
  }

  logout(res) {
    console.log(res);
  }

  byPass() {
    this.router.navigateByUrl('home');
  }



}
